import PageButton from '../../components/button.vue';

export default {
	components: {
		PageButton,
	},

	data() {
		return {
			temp:{
				inquiryId:'',
				detail:'',
				picUrl:'',
				realName:'',
				idCard:"",
				gender:"",
				weight:'',
				allergic:'',
				conceive:'',
				liver:'',
				kidney:'',
				medicalHistory:'',
				states:'',
				doctorId:'',
				doctorName:'',
				introduction:'',
				receptId:'',
				de:'',
				payStates:''
			},
			picUrls:[]
		}
	},
	onLoad(options) {
		this.temp.inquiryId = options.inquiryId;
		this.getInquiryDetail();
	},
	onShow() {
		
	},
	methods: {
		getInquiryDetail:function(){
			let that = this;
			var dd = {"inquiryId":that.temp.inquiryId}
			that.$util.request({
				url: "/didi-patient/inquiryinfo/getDetailInfo" ,
				param: dd,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					that.temp = res.data;
					that.picUrls = that.temp.picUrl.split(',');
					
					that.temp.de = "";
					if(that.$util.isEmpty(res.data.allergic)){
						that.temp.de = that.temp.de +that.$json.allergic[res.data.allergic]+'过敏史/'; 
					}
					if(that.$util.isEmpty(res.data.conceive)){
						that.temp.de = that.temp.de +that.$json.conceive[res.data.conceive]+"/"; 
					}
					if(that.$util.isEmpty(res.data.liver)){
						that.temp.de = that.temp.de +'肝功能'+that.$json.liver[res.data.liver]+"/";
					}
					if(that.$util.isEmpty(res.data.kidney)){
						that.temp.de = that.temp.de +'肾功能'+that.$json.liver[res.data.kidney]; 
					}
				},
				error: function() {}
			})
		},
		commentDoctor:function(){
			uni.navigateTo({
				url:'/pages/my/commentDoctor?detail='+JSON.stringify(this.temp)
			})
		},
		cancelInquiry:function(){
			let that = this;
			var dd = {"inquiryId":that.temp.inquiryId}
			that.$util.request({
				url: "/didi-patient/inquiryinfo/cancelInquiry" ,
				param: dd,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.showToast({
						title: '取消成功',
						icon: 'success',
						duration: 2000
					})
					that.getInquiryDetail();
				},
				error: function() {}
			})
		},
		payInquiry:function(){
			let that = this;
			var temp = {};
			temp.inquiryId = that.temp.inquiryId;
			that.$util.request({
				url: "/didi-patient/patientpayinfo/unifiedOrder",
				param: temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					var data = res.data;
					 //调用微信支付
					uni.requestPayment({
					  'appId': data.appId,
					  'timeStamp': data.timeStamp,
					  'nonceStr': data.nonceStr,
					  'package': data.package,
					  'signType': data.signType,
					  'paySign': data.paySign,
					  'success': function (res) {
					    uni.showToast({
					    	title: '支付成功',
					    	icon:'none',
					    	duration: 2000
					    });
						uni.navigateBack({
							delta:1
						})
					  },
					  'fail': function (res) {
						
					  }
					})
				},
				error: function() {}
			})
		}
		
	}
}
